.bubble-container {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border-width: 1px;
    background-color: hwb(296 85% 2%);
    border-color: black;
    align-content: center;
    margin: 0px 5px 0px 5px;
}

.icon {
    width: 50px;
    height: 50px;
}

.bubble-container:hover {
    background-color: white;
}